<template>
  <div class="Container">
    <Head :show="false"></Head>
    <div class="ContainerBody">
      <h1 class="ContainerBodyChat"><img :src="ChatGpt" alt="" /></h1>
      <div class="ContainerBodyPrompt">当我回答慢的时候，请多等我几秒</div>
    </div>
    <div class="ContainerBottom">
      <Input
        enter-button
        placeholder="说点什么..."
        @on-keydown="
          (e) => {
            // 判断是否是按下了回车
            if (e.keyCode === 13) {
              enterEvent();
            }
          }
        "
        v-model.trim="InputSearch"
      >
        <template #append>
          <Icon type="ios-paper-plane-outline" @click="enterEvent" />
        </template>
      </Input>
    </div>
  </div>
</template>
<script>
import Head from "./components/Head";
// 跳转路由  vue 3 中没有this 跳转路由只能使用  useRouter
import ChatGpt from "@/assets/images/Chat-GPT.png";
import { useRouter } from "vue-router";
import { ref } from "vue";
import { useStore } from "vuex";
export default {
  name: "homeChat",
  // eslint-disable-next-line vue/no-unused-components
  components: { Head },
  setup() {
    const store = useStore();
    const router = useRouter(); // 路由实例
    const InputSearch = ref(""); // 输入框内容
    const enterEvent = () => {
      if (!InputSearch.value) return (InputSearch.value = null); // 如果输入框为空 就不跳转
      router.push({
        name: "ChatGPT",
      }); // 跳转路由 本来是可以携带参数但是他的参数是在url里边不太舒服 还是放在vuex里边吧 隐藏式传参好像被移除了
      store.commit("setInputSearch", InputSearch.value); // 提交到vuex里边提交内容
    };
    return { enterEvent, InputSearch, ChatGpt };
  },
};
</script>

